ą poezją. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mt-5"},[_c('div',{staticClass:"col-sm-6 text-center"},[_c('h2',[_c('span',{staticClass:"material-icons"},[_vm._v("school")])]),_c('h4',[_vm._v("Szkoła średnia")]),_c('h5',[_vm._v("Technikum w Czarnej Białostockiej")]),_c('br'),_c('p',{staticClass:"text-justify"},[_vm._v(" O profilu technik informatyk. W trakcie edukacji w tej szkole zdobyłem podstawową wiedzę oraz praktykę techniczną, która została potwierdzona trzema kwalifikacjami w tym zawodzie (E.12, E.13, E.14). W szkole nauczyłem się m.in. o eksploatacji i konserwacji komputerów osobistych i urządzeń peryferyjnych, sieciach komputerowych, zarządzaniu serwerami (systemy Windows Server oraz dystrybucje linuxowe), zarządzaniu bazami danych, oraz o programowaniu prostych skryptów (najczęściej w języku JavaScript). ")])]),_c('div',{staticClass:"col-sm-6 text-center"},[_c('h2',[_c('span',{staticClass:"material-icons"},[_vm._v("school")])]),_c('h4',[_vm._v("Studia inżynierskie")]),_c('h5',[_vm._v("Politechnika Białostocka")]),_c('br'),_c('p',{staticClass:"text-justify"},[_vm._v(" Ukończyłem kierunek informatyka na Wydziale Informatyki, uzyskałem dyplom inżyniera. Tematem mojej pracy dyplomowej było wykonanie aplikacji internetowej dla agencji nieruchomości (Spring i Angular jako baza technologiczna). W toku tych studiów posiadłem wiedzę nie tylko o zagadnieniach czysto programistycznych (pisanie kodu), ale także o aspektach techniki cyfrowej (jak fizycznie wykonane są układy, jak też jak się je programuje), o bezpieczeństwie sieci komputerowych, zarządzaniu w inżynierii programowania, biometrii czy sztucznej inteligencji. ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mt-5"},[_c('div',{staticClass:"col-sm-12"},[_c('div',{staticClass:"card p-3 bg-first"},[_c('h4',{staticClass:"text-right"},[_vm._v("Technologie")]),_c('